*{
    font-family: 'Ubuntu', sans-serif;
    margin: 0;
  }
  .my-masonry-grid {
    display: flex;
    margin-left: -20px; /* gutter size offset */
    width: auto;
  }
  .my-masonry-grid_column {
    padding-left: 20px; /* gutter size */
    background-clip: padding-box;
  }
  
  /* Style your items */
  .my-masonry-grid_column > div { /* change div to reference your elements you put in <Masonry> */
    margin-bottom: 20px;
  }
  /********** COLOR GENERATOR **********/

  #color-container {
    border: 2px solid #D4D4D4;
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    margin: 0 auto;
    box-sizing: border-box;
    padding: 1em;
  }
  #color-box {
    flex-basis: 200px;
    
  }
  .color-info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
  }
  #color-container h2,
  #color-container p {
    margin-bottom: 0.5em;
    letter-spacing: 0.6px;
    font-family: "Roboto", sans-serif;
  }
  #color-container h2 {
    font-weight: 900;
  }
  #color-container p {
    font-weight: 400;
  }
  #color-container h2,
  #color-cmyk,
  #color-rgb,
  #color-button {
    text-transform: uppercase;
  }
  #color-button {
    font-family: "Roboto", sans-serif;
    margin-bottom: 0.5em;
    margin-top: 0.5em;
    padding-top: 0.5em;
    padding-bottom: 0.5em;
    font-weight: 700;
    font-size: 1.1rem;
    color: #252525;
    background: #ffffff;
    border: 2px solid #252525;
    border-radius: 5px;
  }
  #color-button:hover {
    background: lightgray;
    border: 2px solid #666;
  }